.accordion {
  &.accordion-flush {
    border-top: $accordion-border-width solid $accordion-border-color;
    border-bottom: $accordion-border-width solid $accordion-border-color;

    .accordion-item {
      background-color: transparent;
    }

    .accordion-button {
      padding-right: rem(48px);
      padding-left: 0;
      background-color: transparent;

      &:after {
        right: rem(16px);
      }
    }

    .accordion-body {
      padding-right: rem(48px);
      padding-left: 0;
    }
  }

  &:not(.accordion-flush) {
    .accordion-item {
      border: none;
      border-radius: $accordion-border-radius;

      &:not(:last-child) {
        margin-bottom: rem(24px);
      }
    }

    .accordion-button {
      padding-right: rem(64px);
      border-radius: $accordion-border-radius;

      &:after {
        right: rem(24px);
      }
    }

    .accordion-body {
      padding-right: rem(64px);
    }
  }

  .accordion-button {
    font-weight: inherit;
    font-size: inherit;
    box-shadow: none;

    &:after {
      position: absolute;
      top: $accordion-button-padding-y;
      height: 1lh;
      background-position: right center;
    }

    &:hover {
      color: rgba($accordion-button-color, 0.8);
    }
  }

  .accordion-body {
    padding-top: rem(4px);
  }
}
