@if $use-title-palette {
  @include title-sizes;
}

.text-block,
.remove-last-child-margin {
  > * {
    &:last-child {
      margin-bottom: 0 !important;
    }
  }

  &.row {
    > * {
      &.col,
      &[class^="col-"] {
        &:last-child {
          > * {
            margin-bottom: 0 !important;
          }
        }
      }
    }
  }
}

.text-xxs {
  font-size: $font-size-xxs !important;
  line-height: $line-height-xxs !important;
}

.text-sm {
  font-size: rem(14px) !important;
  line-height: $line-height-sm !important;

  @include media-breakpoint-up(md) {
    font-size: $font-size-sm !important;
  }
}

p {
  @include media-breakpoint-down(md) {
    margin-bottom: rem(24px);
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  // styles for headings
}

h1,
.h1 {
  @include media-breakpoint-down(md) {
    font-size: rem(32px);
  }
}

h2,
.h2 {
  @include media-breakpoint-down(md) {
    font-size: rem(26px);
  }
}

h3,
.h3 {
  @include media-breakpoint-down(md) {
    font-size: rem(24px);
  }
}

h4,
.h4 {
  @include media-breakpoint-down(md) {
    font-size: rem(22px);
  }
}

h5,
.h5 {
  @include media-breakpoint-down(md) {
    font-size: rem(20px);
  }
}

h6,
.h6 {
  @include media-breakpoint-down(md) {
    font-size: rem(18px);
  }
}

.article-text {
  > * {
    &:first-child {
      margin-top: 0 !important;
    }

    &:last-child {
      margin-bottom: 0 !important;
    }
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: rem(40px);

    @include media-breakpoint-up(md) {
      @include margin-top(80px);
    }
  }

  ul {
    padding-left: rem(24px);

    @include media-breakpoint-up(md) {
      padding-left: rem(32px);
    }

    li {
      &:not(:last-child) {
        margin-bottom: rem(12px);
      }
    }
  }
}

.text-with-icon {
  display: flex;
  align-items: center;
  min-height: rem(32px);

  @include media-breakpoint-up(md) {
    min-height: rem(55px);
  }

  &.xxs {
    min-height: rem(12px);

    .text-with-icon__block {
      padding-left: rem(16px);
    }

    .text-with-icon__icon {
      @include size(rem(12px));
    }

    .text-with-icon__icon-img {
      @include size(rem(9px));
    }
  }

  &.sm {
    min-height: rem(24px);

    @include media-breakpoint-up(md) {
      min-height: rem(32px);
    }

    .text-with-icon__block {
      padding-left: rem(32px);

      @include media-breakpoint-up(md) {
        padding-left: rem(44px);
      }
    }

    .text-with-icon__icon {
      @include size(rem(24px));

      @include media-breakpoint-up(md) {
        @include size(rem(32px));
      }
    }

    .text-with-icon__icon-img {
      @include size(rem(14px));

      @include media-breakpoint-up(md) {
        @include size(rem(21px));
      }
    }
  }

  &__block {
    position: relative;
    padding-left: rem(44px);

    @include media-breakpoint-up(md) {
      padding-left: rem(75px);
    }
  }

  &__icon-wrap {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: 1lh;
    height: 1lh;
  }

  &__icon {
    @include size(rem(32px));

    position: relative;
    display: flex;
    flex-shrink: 0;
    background-color: $primary;
    border-radius: $border-radius-pill;

    @include media-breakpoint-up(md) {
      @include size(rem(55px));
    }
  }

  &__icon-img {
    @include absolute-center;
    @include size(rem(18px));

    object-fit: contain;

    @include media-breakpoint-up(md) {
      @include size(rem(32px));
    }
  }
}

