  padding-top: rem(16px);
  background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 88 10' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M2 7.34107C2.61555 7.45618 3.299 7.52077 4.06852 7.52077C11.1842 7.52077 10.927 2 17.7855 2C24.644 2 24.644 7.52077 31.5025 7.52077C38.361 7.52077 38.361 2 45.2195 2C52.078 2 52.078 7.52077 58.9365 7.52077C65.7949 7.52077 65.795 2 72.6534 2C78.5476 2 79.5385 10.0088 86 7.52071' stroke='#{$primary}' stroke-width='4' stroke-linecap='round'/></svg>"));
  background-repeat: no-repeat;
  background-position: top left;
  background-size: rem(84px);
}

.with-bottom-decoration {
  display: inline-flex;
  padding-bottom: rem(12px);
  background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 168 9' preserveAspectRatio='none' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M82 6.84107C82.6155 6.95618 83.299 7.02077 84.0685 7.02077C91.1842 7.02077 90.927 1.5 97.7855 1.5C104.644 1.5 104.644 7.02077 111.502 7.02077C118.361 7.02077 118.361 1.5 125.219 1.5C132.078 1.5 132.078 7.02077 138.936 7.02077C145.795 7.02077 145.795 1.5 152.653 1.5C158.548 1.5 159.538 9.50883 166 7.02071' stroke='#{$primary}' stroke-width='3' stroke-linecap='round'/><path d='M86 6.84107C85.3845 6.95618 84.701 7.02077 83.9315 7.02077C76.8158 7.02077 77.073 1.5 70.2145 1.5C63.356 1.5 63.356 7.02077 56.4975 7.02077C49.639 7.02077 49.639 1.5 42.7805 1.5C35.922 1.5 35.922 7.02077 29.0635 7.02077C22.2051 7.02077 22.205 1.5 15.3466 1.5C9.45237 1.5 8.46153 9.50883 2 7.02071' stroke='#{$primary}' stroke-width='3' stroke-linecap='round'/></svg>"));
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 100% rem(9px);
}

.blockquote-item {
  position: relative;
  margin-bottom: 0;
  padding-top: rem(28px);

  @include media-breakpoint-up(md) {
    padding-top: rem(48px);
  }

  &:after {
    @include font-size(60px);

    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    color: $primary;
    font-weight: $font-weight-bold;
    font-family: $font-family-sans-serif-2;
    line-height: 1;
    transform: scaleX(-1);
    content: "“";
  }
}
