.banner-money-back {
  padding: rem(32px 24px);
  text-align: center;
  background-color: $primary;
  border-radius: $border-radius;

  @include media-breakpoint-up(sm) {
    text-align: left;
  }

  &__img {
    @include responsive-ratio(1, 1);

    position: relative;
    display: inline-flex;
    flex-shrink: 0;
    width: rem(98px);

    @include media-breakpoint-up(xl) {
      width: rem(150px);
    }
  }
}
