.fancybox-button {
  color: $black;
  background-color: $primary;
  transition:
    color 0.3s,
    background-color 0.3s;

  &:hover {
    color: $white;
    background-color: $black;
  }
}

.fancybox-slide {
  @include media-breakpoint-down(sm) {
    padding-right: $container-padding;
    padding-left: $container-padding;
  }
}
