.footer {
  overflow: hidden;
  color: $black;
  background-color: $primary;

  &__top-block {
    padding: rem(40px 0);

    @include media-breakpoint-up(md) {
      @include padding(54px 0 40px);
    }
  }

  &__bottom-block-content {
    position: relative;
    padding: rem(20px 0);

    @include media-breakpoint-up(md) {
      @include padding(32px 0);
    }

    &:before {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 5;
      height: 1px;
      background-color: rgba($secondary, 0.4);
      content: "";

      @include media-breakpoint-up(sm) {
        right: rem(-60px);
        left: rem(-60px);
      }
    }
  }

  &__star-icon {
    font-size: rem(16px);

    @include media-breakpoint-up(md) {
      font-size: rem(24px);
    }
  }

  &__subscribe-form-block {
    @include media-breakpoint-up(lg) {
      max-width: 300px;
      margin: 0 auto;
    }
  }

  &__logo {
    display: inline-flex;
    width: 100%;
    max-width: rem(140px);

    @include media-breakpoint-up(md) {
      max-width: rem(192px);
    }
  }

  &__payment-logo {
    @include responsive-ratio(38, 24);

    position: relative;
    display: inline-flex;
    width: 38px;
  }
}
