.table {
  margin-bottom: 0;
  font-weight: 600;

  &-rounded {
    border: $table-border-width solid $table-border-color;
    border-radius: $border-radius;

    .table-bordered {
      thead {
        tr {
          &:first-child {
            border-top: none;

            th {
              &:first-child {
                border-top-left-radius: $border-radius;
              }

              &:last-child {
                border-top-right-radius: $border-radius;
              }
            }
          }

          th {
            &:first-child {
              border-left: none;
            }

            &:last-child {
              border-right: none;
            }
          }
        }
      }

      tbody {
        tr {
          &:last-child {
            border-bottom: none;

            td {
              &:first-child {
                border-bottom-left-radius: $border-radius;
              }

              &:last-child {
                border-bottom-right-radius: $border-radius;
              }
            }
          }

          td {
            &:first-child {
              border-left: none;
            }

            &:last-child {
              border-right: none;
            }
          }
        }
      }
    }
  }

  .icomoon-check {
    font-size: rem(24px);
  }

  .icon-emoji-cross {
    font-size: rem(20px);
  }

  th,
  td {
    height: rem(66px);
    vertical-align: $table-cell-vertical-align;

    @include media-breakpoint-down(sm) {
      padding: rem(10px);
    }
  }

  thead {
    th {
      background-color: $yellow-light;
    }
  }
}
