.video-block {
  @include responsive-ratio(611, 372);

  position: relative;
  display: block;
  overflow: hidden;
  background-color: $black;
  border-radius: $border-radius;

  &:after {
    @include absolute-cover;
    @include flex-center;

    color: $white;
    font-size: rem(44px);
    font-family: "icomoon";
    background-color: rgba($black, 0.6);
    transition: color 0.3s;
    content: "\e901";

    @include media-breakpoint-up(md) {
      font-size: rem(70px);
    }
  }

  &:hover {
    &:after {
      color: $primary;
    }
  }
}
