.card {
  &-gallery-scroller {
    @include media-breakpoint-down(sm) {
      @include custom-scrollbar(0, transparent, transparent);

      display: flex;
      margin: 0 (-$container-padding);
      overflow: auto;
    }

    &__wrap {
      @include media-breakpoint-down(sm) {
        padding: 0 $container-padding;
      }
    }

    .row {
      @include media-breakpoint-down(sm) {
        flex-wrap: nowrap;
      }
    }

    .card-gallery {
      @include media-breakpoint-down(sm) {
        width: 70vw;
        max-width: rem(294px);
      }
    }
  }

  &-gallery {
    width: 100%;
    padding: rem(22px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    &.lg {
      @include media-breakpoint-up(lg) {
        padding: rem(32px);
      }

      .card-gallery__content {
        @include media-breakpoint-up(lg) {
          padding-top: rem(32px);
        }
      }
    }

    &__img {
      @include responsive-ratio(340, 280);

      position: relative;
      overflow: hidden;
      border-radius: $border-radius;
    }

    &__content {
      padding-top: rem(22px);
      text-align: center;
    }

    &__title-icon {
      @include size(rem(22px));

      display: inline;
      object-fit: contain;
      vertical-align: middle;

      @include media-breakpoint-up(md) {
        @include size(rem(24px));
      }
    }
  }

  &-how-it-works {
    width: 100%;
    padding: rem(24px 24px 40px);
    text-align: center;
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    @include media-breakpoint-up(xl) {
      padding-right: rem(40px);
      padding-left: rem(40px);
    }

    &__icon {
      @include size(rem(80px));
      @include inline-flex-center;

      position: relative;
      margin-bottom: rem(32px);
      background-color: $primary;
      border-radius: $border-radius-pill;
    }

    &__icon-img {
      @include size(32px);

      object-fit: contain;

      @include media-breakpoint-up(md) {
        @include size(40px);
      }
    }
  }

  &-info-list {
    .card-info {
      &:not(:last-child) {
        margin-bottom: 40px;

        @include media-breakpoint-up(md) {
          @include margin-bottom(100px);
        }
      }
    }

    &:not(.reversed) {
      .card-info {
        &:nth-child(2n) {
          .card-info__row {
            @include media-breakpoint-up(lg) {
              flex-direction: row-reverse;
            }
          }

          .card-info__img-col {
            @include media-breakpoint-up(lg) {
              margin-left: 8.33333333%;
            }
          }

          .card-info__content-col {
            @include media-breakpoint-up(lg) {
              margin-left: 0;
            }
          }
        }
      }
    }

    &.reversed {
      .card-info {
        &:nth-child(2n + 1) {
          .card-info__row {
            @include media-breakpoint-up(md) {
              flex-direction: row-reverse;
            }
          }

          .card-info__img-col {
            @include media-breakpoint-up(xl) {
              margin-left: 8.33333333%;
            }
          }

          .card-info__content-col {
            @include media-breakpoint-up(xl) {
              margin-left: 0;
            }
          }
        }
      }
    }
  }

  &-info {
    &.reversed {
      .card-info__row {
        @include media-breakpoint-up(lg) {
          flex-direction: row-reverse;
        }
      }

      .card-info__img-col {
        @include media-breakpoint-up(lg) {
          margin-left: 8.33333333%;
        }
      }

      .card-info__content-col {
        @include media-breakpoint-up(lg) {
          margin-left: 0;
        }
      }
    }

    &.centered {
      .card-info__row {
        align-items: center;
      }
    }

    &.reduced-img {
      .card-info__img {
        @include responsive-ratio(610, 380);
      }
    }

    &__row {
      @include media-breakpoint-down(lg) {
        flex-direction: column-reverse;
      }
    }

    &__content-col {
      @include media-breakpoint-up(lg) {
        margin-left: 8.33333333%;
      }
    }

    &__img {
      @include responsive-ratio(612, 450);

      position: relative;
      overflow: hidden;
      background-color: $gray-light;
      border-radius: $border-radius-xl;
    }

    &__btn {
      padding-top: rem(24px);

      @include media-breakpoint-up(lg) {
        padding-top: rem(32px);
      }
    }
  }

  &-product {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: rem(40px 24px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    @include media-breakpoint-up(md) {
      padding-right: rem(32px);
      padding-left: rem(32px);
    }

    &.featured {
      border: 2px solid $dark;
    }

    &__badge {
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 5;
      margin-bottom: 0;
      padding: rem(6px 16px);
      white-space: nowrap;
      text-align: center;
      text-transform: uppercase;
      background-color: $primary;
      border: 2px solid $dark;
      border-radius: $border-radius-pill;
      transform: translate(-50%, -50%);
    }

    &__img {
      @include responsive-ratio(432, 332);

      position: relative;
      overflow: hidden;
      border-radius: $border-radius;
    }

    &__content {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      padding-top: rem(24px);
    }

    &__title-and-price-block {
      display: flex;
      align-items: center;
      margin-bottom: rem(32px);

      @include media-breakpoint-up(md) {
        margin-bottom: rem(40px);
      }
    }

    &__top-content {
      flex-grow: 1;
    }

    &__bottom-content {
      padding-top: rem(60px);
      text-align: center;
    }
  }

  &-purchase-review {
    padding: rem(32px 24px);
    border: 1.5px solid $secondary;
    border-radius: $border-radius;

    &__author {
      padding-top: rem(24px);

      @include media-breakpoint-up(sm) {
        padding-top: rem(32px);
      }
    }

    &__author-img {
      @include responsive-ratio(1, 1);

      position: relative;
      width: rem(45px);
      overflow: hidden;
      background-color: $gray-light;
      border-radius: $border-radius-pill;

      @include media-breakpoint-up(sm) {
        width: rem(65px);
      }
    }
  }
}
