.btn {
  @include inline-flex-center;

  text-transform: uppercase;

  // sizes
  &:not(.btn-sm, .btn-lg) {
    min-height: rem(54px);

    @include media-breakpoint-up(sm) {
      min-width: rem(300px);
    }
  }

  // colors
  &-primary {
    @include button-variant($primary, $primary, $dark, $black, $black, $white, $black, $black, $white);
  }

  &-red,
  &-danger {
    @include button-variant($red, $red, $white, $red-dark, $red-dark, $white, $red-dark, $red-dark, $white);
  }
}
