label {
  &.form-label {
    cursor: pointer;
  }
}

.form-control {
  caret-color: $input-color;
  text-overflow: ellipsis;
  box-shadow: none !important;

  &[disabled] {
    -webkit-text-fill-color: $input-disabled-color;
    cursor: not-allowed;
  }

  @include media-breakpoint-down(md) {
    padding: rem(12px);
    font-size: rem(16px);
  }
}

textarea {
  &.form-control {
    height: 200px;
  }
}

.form-select {
  color: $input-placeholder-color;

  &:not([disabled]) {
    cursor: pointer;
  }

  &[disabled] {
    cursor: not-allowed;
  }

  @include media-breakpoint-down(md) {
    padding: rem(rem(12px) calc(rem(12px) + rem(12px) + rem(16px)) rem(12px) rem(12px));
    font-size: rem(16px);
    background-position: right rem(12px) center;
  }
}

.position-relative {
  > .invalid-feedback {
    position: absolute;
    top: calc(100% + $form-feedback-margin-top);
    left: 0;
    z-index: 5;
    margin-top: 0;
  }
}

.invalid-feedback {
  @include media-breakpoint-down(md) {
    margin-top: rem(4px);
    font-size: rem(12px);
  }
}
