.form-subscribe {
  position: relative;

  &__input {
    @include placeholder {
      color: $dark;
    }

    width: 100%;
    height: rem(40px);
    padding: rem(8px 72px 8px 24px);
    color: $dark;
    font-weight: $font-weight-semibold;
    font-size: rem(16px);
    background-color: transparent;
    border: 1px solid $dark;
    border-radius: $border-radius-pill;

    @include media-breakpoint-up(md) {
      height: rem(48px);
      font-size: rem(20px);
    }
  }

  &__btn {
    @include btn-reset;

    position: absolute;
    top: rem(8px);
    right: rem(24px);
    z-index: 5;
    display: inline-flex;
    color: $dark;
    font-size: rem(24px);
    transition: transform 0.3s;

    @include media-breakpoint-up(md) {
      top: rem(11px);
    }

    &:hover {
      transform: rotate(-45deg);
    }
  }
}
