.list {
  &-with-check {
    @include list-reset;

    &.text-sm {
      li {
        &:not(:last-child) {
          margin-bottom: rem(12px);
        }
      }
    }

    li {
      position: relative;
      padding-left: rem(24px);

      &:not(:last-child) {
        margin-bottom: rem(24px);
      }

      &:before {
        @include size(1lh);

        position: absolute;
        top: 0;
        left: 0;
        z-index: 5;
        display: inline-flex;
        align-items: center;
        background-image: escape-svg(url("data:image/svg+xml,<svg fill='none' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'><g clip-path='url(#a)'><path d='M14.2 1.486c-2.12 1.862-3.912 3.634-5.554 5.947-.724 1.02-1.529 2.22-2.055 3.354-.3.6-.842 1.54-1.027 2.443-1.01-.954-2.094-2.036-3.204-2.884-.79-.604-3.07.627-2.142 1.336 1.662 1.269 3.045 2.85 4.661 4.175.677.554 2.175-.649 2.527-1.153 1.156-1.663 1.314-3.695 2.157-5.509 1.286-2.773 3.567-5.051 5.82-7.03 1.492-1.413-.05-1.673-1.18-.679' fill='#{$dark}'/></g></svg>"));
        background-repeat: no-repeat;
        background-position: center left;
        background-size: rem(16px);
        content: "";
      }
    }
  }
}
