.section {
  padding: rem(40px 0);

  @include media-breakpoint-up(md) {
    @include padding(100px 0);
  }

  &.smaller-indents {
    @include media-breakpoint-up(md) {
      @include padding(60px 0);
    }
  }

  &-welcome {
    position: relative;
    background-color: $white;

    @include media-breakpoint-down(md) {
      padding-top: 50vw;
    }

    @include media-breakpoint-up(md) {
      color: $white-light;
    }

    @include media-breakpoint-up(xl) {
      display: flex;
      align-items: center;
      min-height: rem(700px);
    }

    &__img {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      background-color: $secondary;
      pointer-events: none;

      @include media-breakpoint-up(md) {
        bottom: 0;
      }

      @include media-breakpoint-down(md) {
        @include responsive-ratio(375, 200);
      }

      &:after {
        @include absolute-cover;

        z-index: 1;
        display: block;
        background: linear-gradient(to top, $light 5%, rgba($light, 0.25) 35%, rgba($light, 0) 67.04%);
        content: "";
        pointer-events: none;

        @include media-breakpoint-up(md) {
          background: linear-gradient(270deg, rgba($black, 0) 30.76%, rgba($black, 0.1) 100%);
        }
      }
    }

    &__title {
      color: $dark;

      @include media-breakpoint-up(md) {
        color: inherit;
      }
    }
  }

  &-banner {
    background-color: $primary;

    @include media-breakpoint-up(md) {
      padding: rem(32px 0);
    }

    &__img {
      @include responsive-ratio(192, 178);

      position: relative;
      max-width: rem(192px);
      margin: 0 auto;
      overflow: hidden;
    }
  }
}
