.of-contain {
  @include absolute-cover;

  object-fit: contain;
}

.of-cover {
  @include absolute-cover;

  object-fit: cover;
}

%custom-scrollbar {
  @include custom-scrollbar(8px, $secondary, $primary, $border-radius-pill);
}

.custom-scrollbar {
  @extend %custom-scrollbar;
}

%custom-scrollbar-thin {
  @include custom-scrollbar(6px, $yellow-light, $dark, $border-radius-pill, 1px);
}

.custom-scrollbar-thin {
  @extend %custom-scrollbar;
}

.tabular-nums {
  font-variant-numeric: tabular-nums;
}

.resize-animation-stopper * {
  transition: none !important;
}

.w-sm-auto {
  @include media-breakpoint-up(sm) {
    width: auto !important;
  }
}

.divider-col {
  position: relative;

  &:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    z-index: 5;
    display: block;
    width: 1px;
    height: 100%;
    background-color: $gray;
    transform: translate(-50%, 0);
    content: "";
  }
}
