.price-item {
  display: inline-flex;
  padding: 0.3076923em 0.46153846em;
  background-color: $primary;
}

.price-item-crossed {
  position: relative;
  display: inline-flex;

  &:after {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 5;
    display: block;
    width: 105%;
    height: rem(2px);
    background-color: $danger;
    border-radius: $border-radius-pill;
    transform: translate(-50%, -50%) rotate(-10deg);
    content: "";
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  &.price-item-crossed {
    &:after {
      height: rem(4px);
    }
  }
}
